  computed: {
    generatedUrl() {
      if (!this.selectedApp) return ''
      
      let url = this.selectedApp.scheme
      const params = []
      
      // æ„å»ºURLå‚æ•°
      this.selectedApp.parameters.forEach(param => {
        const value = this.paramValues[param.key]
        if (value) {
          if (param.key === 'action' && this.selectedApp.id === 'alipay') {
            url += `${value}`
          } else if (param.key === 'action' && this.selectedApp.id === 'wechat') {
            url += `${value}/`
          } else if (param.key === 'action' && this.selectedApp.id === 'qq') {
            url += `${value}?`
          } else if (param.key === 'action' && this.selectedApp.id === 'taobao') {
            url += `${value}?`
